<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 border" style="display: block;">
            <div class="row">
                <div class="clearfix"></div>
                <div class="col-lg-12 mb-3">
                    <div class="heading-border mt-2 mb-2">
                        <h5 class="h5"><span>Work Order Detail</span> </h5>
                    </div>
                    <dynamic-form class="" lang="en" :buttons="buttons" :schema="FormSchema">
                        <template v-slot:tgslot-Description="{ data }">
                            <textarea class="form-control form-control:disabled" readonly disabled="disabled"
                                :value="data.fieldInfo.value"></textarea>
                        </template>
                    </dynamic-form>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-6 mb-3">
                    <div class="heading-border mt-2 mb-2">
                        <h5 class="h5"><span>Scheduled Time</span> </h5>
                    </div>
                    <dynamic-form class="" lang="en" :buttons="buttons" :schema="ScheduledSchema"></dynamic-form>
                </div>
                <div class="col-lg-6 mb-3">
                    <div class="heading-border mt-2 mb-2">
                        <h5 class="h5"><span>Preferred Time</span> </h5>
                    </div>
                    <dynamic-form class="" lang="en" :buttons="buttons" :schema="PreferredSchema"></dynamic-form>
                </div>
            </div>
            <div class="row">
                <div class="clearfix"></div>
                <div class="col-lg-12 mb-3">
                    <div class="heading-border mt-2 mb-2">
                        <h5 class="h5"><span>Status Detail</span> </h5>
                    </div>
                    <dynamic-form lang="en" :schema="locateFormSchema" v-if="isLocateorNonLocate"></dynamic-form>

                    <dynamic-form lang="en" :schema="nonLocateFormSchema" v-if="!isLocateorNonLocate"></dynamic-form>


                    <div class="listing" v-if="formListForSubmit.length > 0">
                        <div class="table-responsive table-fix-header mb-2">
                            <table class="table table-bordered dt-responsive nowrap mt-0" id="tbl_formList">
                                <thead class="thead-bg">
                                    <tr>
                                        <th class="sorting"><span>{{ $t('FormName') }}</span></th>
                                        <th class="text-center"><span>{{ $t('FormSubmitted') }}</span></th>
                                        <th class="text-center sorting"><span>{{ $t('Action') }}</span></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(row, index) in formListForSubmit">
                                        <td>
                                            {{ row.name }} <span class="text-danger font-weight-bold"
                                                v-if="row.second_value == '1'">*</span></td>
                                        <td class="text-center">
                                            <span class="px-2 bg-success text-white text-center"
                                                v-if="checkFormSubmitted(row.value) == 'Yes'">{{
                                                    checkFormSubmitted(row.value) }}</span>
                                            <span class="px-2 bg-danger text-white text-center"
                                                v-if="checkFormSubmitted(row.value) == 'No'">{{
                                                    checkFormSubmitted(row.value) }}</span>
                                        </td>
                                        <td class="text-center" v-if="EditEnabled == false"><a href="javascript:;"><i
                                                    class="fa fa-edit fa-lg action-icon text-success disabled"></i></a>
                                        </td>
                                        <td class="text-center" v-if="EditEnabled"><a href="javascript:;"
                                                v-on:click="GetCustomFieldFormForEdit(row)"><i
                                                    class="fa fa-edit fa-lg action-icon text-success"></i></a></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div class="row flex-row-reverse mt-2">
                        <div class="col-lg-6 text-right">

                            <a href="javascript:;" v-if="!isLocateorNonLocate && formListForSubmit.length == 0"
                                class="btn btn-success formbtn text-white" :title="$t('UpdateStatus')"
                                v-on:click="WorkOrderUpdateStatus()" id="btnSave"><i class="fa fa-save pr-2"></i>{{
                                    $t('UpdateStatus') }}</a>

                            <a href="javascript:;" v-if="!isLocateorNonLocate && formListForSubmit.length > 0"
                                :title="$t('SaveWorkOrder')" v-on:click="SubmitUpdateStatus()"
                                class="btn btn-primary save-btn SubmitUpdate"><i class="fa fa-floppy-o pr-2"
                                    aria-hidden="true"></i> {{ $t('SaveWorkOrder') }}</a>



                            <a href="javascript:;" id="WOStatus" :title="$t('UpdateStatus')" v-if="isLocateorNonLocate"
                                class="btn btn-primary save-btn WorkOrderStatus mr-2" v-on:click="WorkOrderStatus()"><i
                                    class="fa fa-cog pr-2" aria-hidden="true"></i><span> {{ $t('UpdateStatus') }}</span></a>

                            <a href="javascript:;" :title="$t('SaveWorkOrder')" v-if="isLocateorNonLocate"
                                v-on:click="SubmitUpdateStatus()" class="btn btn-primary save-btn SubmitUpdate"><i
                                    class="fa fa-floppy-o pr-2" aria-hidden="true"></i> {{ $t('SaveWorkOrder') }}</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <transition name="modal">
            <div class="my-popups">
                <div class="modal d-block mail-box-pop dialog" v-if="workOrderModalStatus">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title">{{ $t('WorkOrder') + ' ' + $t('Status') }}</h4>
                                <button type="button" class="close" v-on:click="Close">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">
                                <dynamic-form lang="en" :schema="nonLocateFormSchema">
                                    <template v-slot:tgslot-Description="{ data }">
                                        {{ data.fieldInfo }}
                                    </template>
                                </dynamic-form>
                            </div>
                            <div class="modal-footer">
                                <a tabindex="13" class="btn btn-success formbtn text-white" :title="$t('Save')"
                                    v-on:click="SubmitWOrkOrderUpdateStatus" id="btnSave"><i class="fa fa-save pr-2"></i>
                                    {{ $t('Save') }}</a>
                                <a tabindex="14" v-on:click="Close" :title="$t('Cancel')" id="btnCancel"
                                    class="btn btn-danger"><i class="fa fa-times"></i> {{ $t('Cancel') }}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </transition>
        <EditViewFSMForm :isforshow="false" :formIdForShow="formIdForShow" :listForSubmittedForm="listForSubmittedForm"
            :moduleName="moduleName" :subModuleCode="subModuleCode" :workOrderId="workOrderId" :subStatusId="subStatusId"
            :callbackfunction="callbackfunction" v-if="ShowWorkOrderStatusForm"></EditViewFSMForm>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import EditViewFSMForm from "../../components/Common/ViewFSMForm.vue";
export default {
    components: {
        EditViewFSMForm
    },
    props: {
        workOrderDetailData: {
            type: Array,
            required: false
        },
        workOrderId: {
            ype: Number,
            required: false
        },
        isLocateorNonLocate: {
            type: String,
            required: true
        },
        ticketId: {
            type: Number,
            required: false
        },
        WO_Status_Id: {
            type: Number,
            required: false
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: { "required": false, max: 1000 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('WO Number'),
                            name: "workorder_number",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Estimation Duration'),
                            name: "estimate_duration",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Ticket Number'),
                            name: "ticket_number",
                            disabled: true,
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Created Date'),
                            name: "created_at",
                            disabled: true,
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Created BY'),
                            name: "created_by",
                            disabled: true,
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Work Type'),
                            name: "work_type_id",
                            disabled: true,
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Client'),
                            name: "client",
                            disabled: true,
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Address'),
                            name: "address",
                            disabled: true,
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Subject'),
                            disabled: true,
                            name: "subject",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                        },
                    ]
                },
            ],
            ScheduledSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Scheduled Start Date Time'),
                            name: "SA_Scheduled_StartTime",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Scheduled End Date Time'),
                            name: "SA_Scheduled_EndTime",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: { "required": false, max: 200 },
                        }
                    ]
                },
            ],
            PreferredSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Preferred Start Date Time:'),
                            name: "StartDateTime",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: { "required": false, max: 200 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Preferred End Date Time'),
                            name: "EndDateTime",
                            value: "",
                            placeholder: "",
                            disabled: true,
                            validationRules: { "required": false, max: 200 },
                        }
                    ]
                },
            ],
            locateFormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.SetParentSubStatus
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('SubStatus'),
                            name: "SubStatus",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetFormsOnBehalfOfSubStatus
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                }
            ],
            nonLocateFormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.onChangeStatus
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                }
            ],
            isLoading: false,
            moduleName: 'TICKETING',
            subModuleCode: 'TICKETING',
            listForSubmittedForm: [],
            Description: null,
            StatusData: [],
            SubStatusData: [],
            refId: 0,
            subStatusId: 0,
            fsmType: null,
            formIdForShow: 0,
            formListForSubmit: [],
            EditEnabled: false,
            workOrderTicketId: 0,
            IsAction: false,
            workOrderId: 0,
            WorkOrderStatusId: '',
            workOrderModalStatus: false,
            ShowWorkOrderStatusForm: false
        }
    },
    created: function () {
        var vm = this;
        setTimeout(() => {

            vm.GetDetail();
        }, 3000);
        setTimeout(() => {
            vm.workOrderTicketId = vm.ticketId;
            vm.workOrderId = vm.workOrderId;
            if (vm.WO_Status_Id != '') {
                vm.WorkOrderStatusId = vm.WO_Status_Id;
            }
            vm.GetDropDownData('WORK_ORDER_FINAL_STATUS');
            vm.GetUpdateStatusDataForEdit(vm.workOrderId, vm.workOrderTicketId);
            if (vm.isLocateorNonLocate == true) {
                vm.GetFSMDDLData("FSM_WORK_ORDER", vm.workOrderTicketId, vm.workOrderId);
            }
        }, 4000);
    },

    methods: {
        SubmitWOrkOrderUpdateStatus: async function () {
            var vm = this;
            var jsonobj =
            {
                Id: vm.workOrderId,
                StatusId: vm._data.nonLocateFormSchema[0].Data[0].value
            }
            var params = JSON.stringify(jsonobj);
            await DataService.MarkAsDone(params).then(function (response) {
                if (response.data == 1) {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.callbackfunction();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Status")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            this.workOrderModalStatus = false;
        },
        WorkOrderStatus: function () {
            var vm = this;
            vm.workOrderModalStatus = !vm.workOrderModalStatus;
        },
        GetUpdateStatusDataForEdit: async function (WorkOrderId, WorkOrderTicketId) {
            var vueObj = this;
            var param = `TicketId=${WorkOrderTicketId}&WOId=${WorkOrderId}`;
            await DataService.GetSingleWorkOrder(param).then(function (response) {
                if (response.data.status == "Success") {

                    var json = response.data.result
                    if (json != null) {
                        if (vueObj.isLocateorNonLocate == true) {
                            vueObj.ParentSubStatusId = json.PARENT_SUB_STATUS_ID == null ? '' : json.PARENT_SUB_STATUS_ID;
                            vueObj.ChildSubStatusId = json.SUB_STATUS_ID == null ? '' : json.SUB_STATUS_ID;
                            vueObj.WorkOrderStatusId = json.STATUS_ID == null ? '' : json.STATUS_ID;
                            vueObj.ParentSubStatusName = json.PARENT_SUB_STATUS_NAME;
                            vueObj.ChildSubStatusName = json.CHILD_SUB_STATUS_NAME;
                            vueObj.Description = json.COMMENT;
                            vueObj.SetParentSubStatus(WorkOrderId);

                            vueObj.MinutesWorked = json.MINUTES_WORKED > 0 ? json.MINUTES_WORKED : '';
                            vueObj.FeetLocated = json.FEET_LOCATED > 0 ? json.FEET_LOCATED : '';
                            if (vueObj.ChildSubStatusId > 0) {
                                vueObj.GetFormsOnBehalfOfSubStatus();
                            }
                        }
                        else {
                            vueObj._data.nonLocateFormSchema[0].Data[0].value = vueObj.WorkOrderStatusId;
                            vueObj.GetFormsWithoutSubStatus();
                        }
                    }
                }
            });
        },
        WorkOrderUpdateStatus: async function () {
            var vm = this;
            var jsonobj =
            {
                Id: vm.workOrderId,
                StatusId: vm.WorkOrderStatusId
            }
            var params = JSON.stringify(jsonobj);
            await DataService.MarkAsDone(params).then(function (response) {
                if (response.data == 1) {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.callbackfunction();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Status")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        SetParentSubStatus: function () {
            var vueObj = this;
            vueObj.GetFSMDDLData("FSM_WORK_ORDER_CHILD_SUB_STATUS", vueObj.workOrderTicketId, vueObj.workOrderId);
            vueObj.GetFormsOnBehalfOfSubStatus(vueObj._data.locateFormSchema[0].Data[0].value);
        },
        GetDropDownData: async function (type) {
            var vm = this;
            var param = `id=&moduleName=Ticketing&type=${type}&search=&refId=`;
            vm.workorderStatusDataList = [];
            await DataService.GetDDLData(param).then(function (response) {
                if (response.data.DATA.length > 0) {
                    var json = response.data.DATA;
                    vm.workorderStatusDataList = response.data.DATA;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm._data.nonLocateFormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                    let selectedStatus = json.find(x => x.value == vm.WorkOrderStatusId);
                    if (selectedStatus.name == "Cancelled" || selectedStatus.name == "Completed") {
                        vm.EditEnabled = false;
                        vm.IsAction = true;
                    }
                    else {
                        vm.EditEnabled = true;
                    }
                }
            });
        },
        onChangeStatus: function (event) {
            var vm = this;
            vm.WorkOrderStatusId = event.target.value;
            let selectedStatus = vm.workorderStatusDataList.find(x => x.value == vm.WorkOrderStatusId);
            if ((selectedStatus.name == "Cancelled" || selectedStatus.name == "Completed") && vm.IsAction == true) {
                vm.EditEnabled = false;
            }
            else {
                vm.EditEnabled = true;
            }
        },
        GetDetail: function () {
            var vm = this;
            debugger;
            if (vm.workOrderDetailData.length > 0) {
                vm.workOrderDetailData.forEach(function (item) {
                    if (item.display_name == 'Description') {
                        if (!vm.isBlank(item.value)) {
                            vm.FormSchema[0].Data[0].value = item.value.replace(/<\/?[^>]+(>|$)/g,"");
                        }
                    }
                    else if (item.display_name == 'WO Number') {
                        vm.FormSchema[0].Data[1].value = item.value;
                    }
                    else if (item.display_name == 'Estimation Duration') {
                        vm.FormSchema[0].Data[2].value = item.value;
                    }
                    else if (item.display_name == 'Ticket number') {
                        vm.FormSchema[0].Data[3].value = item.value;
                    }
                    else if (item.display_name == 'Created Date') {
                        vm.FormSchema[0].Data[4].value = item.value;
                    }
                    else if (item.display_name == 'Created by') {
                        vm.FormSchema[0].Data[5].value = JSON.parse(item.value)[0].name;
                    }
                    else if (item.display_name == 'Work type') {
                        vm.FormSchema[0].Data[6].value = vm.FilterDDLandNormalValue(item.value);
                    }
                    else if (item.display_name == 'Client') {
                        vm.FormSchema[0].Data[7].value = JSON.parse(item.value)[0].name;
                    }
                    else if (item.display_name == 'Address') {
                        vm.FormSchema[0].Data[8].value = item.value;
                    }
                    else if (item.display_name == 'Subject') {
                        vm.FormSchema[0].Data[9].value = item.value;
                    }
                    else if (item.display_name == 'Preferred Start Date Time') {
                        vm.PreferredSchema[0].Data[0].value = item.value;
                    }
                    else if (item.display_name == 'Preferred End Date Time') {
                        vm.PreferredSchema[0].Data[1].value = item.value;
                    }
                    else if (item.display_name == 'Scheduled Start Date Time') {
                        vm.ScheduledSchema[0].Data[0].value = vm.$options.filters.formatDateTime(item.value);
                    }
                    else if (item.display_name == 'Scheduled End Date Time') {
                        vm.ScheduledSchema[0].Data[1].value = vm.$options.filters.formatDateTime(item.value);
                    }
                })
            }

        },
        checkFormSubmitted: function (formId) {
            var vm = this;
            var returnValue = 'No';
            $.each(vm.listForSubmittedForm, function (index, item) {
                if (item.formId == formId && item.formSubmittedId > 0) {
                    returnValue = 'Yes';
                }
            });
            return returnValue;
        },
        GetCustomFieldFormForEdit: function (item) {
            var vm = this;
            vm.formIdForShow = item.value;
            vm.subStatusId = vm._data.locateFormSchema[0].Data[1].value;
            vm.ShowWorkOrderStatusForm = !vm.ShowWorkOrderStatusForm;

        },
        GetFormsOnBehalfOfSubStatus: async function (parentStatusCodeId) {
            var vm = this;
            let refId = vm._data.locateFormSchema[0].Data[1].value;
            vm.formListForSubmit = [];
            vm.listForSubmittedForm = [];
            var param = `id=${vm.workOrderId}&moduleName=TICKETING&type=FSM_FORMS_BY_STATUS&search=&refId=` + refId;
            await DataService.GetDDLData(param).then(function (response) {
                if (!vm.isBlank(response.data) && response.data.DATA.length > 0) {
                    vm.formListForSubmit = response.data.DATA;
                    $.each(response.data.DATA, function (index, item) {
                        var obj = {
                            formId: item.value,
                            formSubmittedId: item.first_value,
                            isAllFormMandatory: item.second_value
                        }
                        vm.listForSubmittedForm.push(obj);
                    });
                }
            });
        },
        GetFormsWithoutSubStatus: async function () {
            var vm = this;
            vm.formListForSubmit = [];
            var param = `id=${vm.workOrderId}&moduleName=TICKETING&type=FSM_FORMS_WITHOUT_STATUS_FOR_NON_LOCATE&search=&refId=`;
            await DataService.GetDDLData(param).then(function (response) {
                if (!vm.isBlank(response.data) && response.data.DATA.length > 0) {
                    vm.formListForSubmit = response.data.DATA;
                    $.each(response.data.DATA, function (index, item) {
                        var obj = {
                            formId: item.value,
                            formSubmittedId: item.first_value,
                            isAllFormMandatory: item.second_value
                        }
                        vm.listForSubmittedForm.push(obj);
                    });
                }
            });
        },
        GetFSMDDLData: async function (type, workOrderTicketId, workOrderId) {
            var vm = this;
            var json = "";
            var refId = "";
            if (type == "FSM_WORK_ORDER") {
                refId = "";
            }
            refId = vm._data.locateFormSchema[0].Data[0].value;
            vm._data.locateFormSchema[0].Data[1].config.options = [];

            var param = "id=" + workOrderTicketId + "&moduleName=&type=" + type + "&refId=" + refId + "&workOrderId=" + workOrderId;
            await DataService.GetFSMDDLData(param).then(function (response) {
                if (!vm.isBlank(response.data) && response.data.status == "Success") {
                    json = response.data.result.DATA;
                    if (json != null) {
                        if (type == "FSM_WORK_ORDER") {
                            json.forEach((item, index) => {
                                vm._data.locateFormSchema[0].Data[0].config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })

                        }
                        else if (type == "FSM_WORK_ORDER_CHILD_SUB_STATUS") {
                            json.forEach((item, index) => {
                                vm._data.locateFormSchema[0].Data[1].config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })

                        }
                    }
                    else {
                        if (type == "FSM_WORK_ORDER_CHILD_SUB_STATUS") {
                            vm._data.locateFormSchema[0].Data[1].value = "";
                        }
                    }
                }
            });
        },
        SubmitUpdateStatus(formfields, val) {
            var vueObj = this;
            let selectedStatus = vueObj.workorderStatusDataList.find(x => x.value == vueObj.WorkOrderStatusId);
            if (selectedStatus.name != "Cancelled") {
                $.each(vueObj.listForSubmittedForm, function (index, item) {
                    if ((item.formSubnittedId == null || item.formSubnittedId == 0) && item.isAllFormMandatory == "1") {
                        // IsFormValid = false;
                    }
                });
                //}
                $.each(vueObj.listForSubmittedForm, function (index, item) {
                    if (item.hasOwnProperty('SaveFormJSON') == true) {
                        delete item.SaveFormJSON;
                    }
                });
            }
            vueObj.isLoading = true;
            var jsonobj = {
                WorkOrderId: vueObj.workOrderId,
                TicketId: vueObj.workOrderTicketId,
                ParentSubStatusId: vueObj._data.locateFormSchema[0].Data[0].value,
                ChildSubStatusId: vueObj._data.locateFormSchema[0].Data[1].value,
                Description: '',
                IsCornerLot: 0,
                IsGasfacility: 0,
                IsCastIron: 0,
                IsPrivateLocateOpportunity: 0,
                IsGasPrintIssue: 0,
                NumberofCIMains: '',
                MainSizeAndDistance: '',
                FormSaveId: 0,
                formBaseString: vueObj.listForSubmittedForm,
                MinutesWorked: 0,
                FeetLocated: 0,
                ReqFrom: 'WEB',
                WorkOrderStatusId: vueObj.WorkOrderStatusId
            };
            var params = JSON.stringify(jsonobj);
            DataService.UpdateSubStatus(params).then((response) => {
                if (response.data.result.MSG == 'SUCCESS') {
                    vueObj.ShowAlert(vueObj.$t("UpdatedSuccess", [vueObj.$t("Status")]), "success", true, vueObj.$t("Alert"));
                    vueObj.callbackfunction();
                    setTimeout(function () {
                        vueObj.isLoading = false;
                    }, 500);

                }
                else {
                    vueObj.ShowAlert(vm.$t("ErrorWhileSavingRecords", [vueObj.$t("Status")]), "failure", true, vueObj.$t("Alert"));
                }
            });
        }

    }
}
</script>
