<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full">
                            <h4 class="modal-title">{{ $t('Add Incident') }}</h4>
                            <button type="button" class="close" v-on:click="$emit('Close')">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchemaData" v-on:OnSubmit="SubmitReportIncident"></dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <showReportIncidentModalVue v-if="showIncidentPopUp" @close="close"></showReportIncidentModalVue>
        <showReportSubIncidentModalVue v-if="showSubIncidentPopUp" @close="close"></showReportSubIncidentModalVue>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import showReportIncidentModalVue from '../../components/Modal/showReportIncidentModal.vue';
import showReportSubIncidentModalVue from '../../components/Modal/showReportSubIncidentModal.vue';
export default {
    components:{
        showReportIncidentModalVue,
        showReportSubIncidentModalVue
    },
    props:{
        ticketId:{
            required:false
        },
        workOrderId:{
            required:false
        },
        EditIncidentData:{
          required:false
        }
    },
    created:function(){
        debugger
        var vm=this;
        if (vm.EditIncidentData!=undefined && vm.EditIncidentData.length>0){
            vm.EditData();
        }
        vm.GetDDLData();
    },
    data(){
        return{
            FormSchemaData: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Incident Type'),
                            name: "ParentIncidentId",
                            value: "",
                            config:{
                                showAddIcon:true,
                                onChange:this.OnChange,
                                onAddButtonClick: this.openIncidentType,
                                options:[]
                            },
                            placeholder: "",
                            validationRules: { "required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Incident Sub Type'),
                            name: "ChildSubIncidentId",
                            placeholder: "",
                            validationRules: { "required": true},
                            FieldIndex: 0,
                            config: {
                                showAddIcon:true,
                                onChange:this.SubOnChange,
                                onAddButtonClick: this.openSubIncidentType,
                                options:[]
                            },
                        },
                    ],
                    
                },
                {
                layoutType:"single",
                    Data: [
                         {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Description",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                        },
                        {
                            astype: "FileField",
                            label: this.$t('Attachment'),
                            name: "Attachment",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'pdf',
                                        iconClass: 'fa-file-archive-o'
                                    }
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: "",
                                multiple: true
                            },
                        }
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Report Incident"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            IncidentData:[],
            showIncidentPopUp:false,
            showSubIncidentPopUp:false,
            ParentIncidentId:0,
            IncidentTypeList:[],
            IncidentSubTypeList:[],
            fileData:[],
            fileName:'',
            fileSize:'',
            IncidentId:0,
            AttachmentList:[],
            ChildSubIncidentId:0
        }
    },
    methods:{
        EditData:function(){
            var vm=this;
            vm.FormSchemaData[0].Data[0].value=vm.EditIncidentData[0].incident_type_id;
            vm.FormSchemaData[0].Data[1].value=vm.EditIncidentData[0].incident_subtype_id;
        },
        SubOnChange:function(e){
            var vm=this;
            vm.ChildSubIncidentId=e.target.value;
        },
        openSubIncidentType:function(){
            var vm=this;
            vm.showSubIncidentPopUp=true;
        },
        openIncidentType:function(){
            var vm=this;
            vm.showIncidentPopUp=true;
        },
        close:function(){
            var vm=this;
            vm.showIncidentPopUp=false;
            vm.showSubIncidentPopUp=false;
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    debugger;
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            id: vm.id,
                            moduleName: vm.modulename,
                            subModuleCode: vm.submodulecode,
                            base64String: base64StringEncoded,
                            fileSize: fileSize
                        };

                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        GetDDLData:function(){
            var vm=this;
            var type='FSM_REPORT_INCIDENT';
            var params=`id=${vm.ticketId}&moduleName=Ticketing&type=${type}`
            DataServices.GetDDLData(params).then(function (response) {
                var json = response.data.DATA;
                response.data.DATA.forEach(function (item) {
                    vm.IncidentTypeList.push({
                        name:item.name,
                        value:item.value
                    })
                    vm.FormSchemaData[0].Data[0].config.options.push({
                        name:item.name,
                        value:item.value
                    })
                })
            })
        },
        SubmitReportIncident: function () {
                var vueObj = this;
                var chkError = 0;
                vueObj.FormSchemaData
                var attchobj = [];
                var objectAttachment = null;
                if (typeof (vueObj.fileData) != 'undefined') {
                    if (vueObj.fileData.length > 0) {
                        for (var i = 0; i < vueObj.fileData.length; i++) {
                            var val = '.' + vueObj.fileData[i].extensionName
                            var arrindx = $.inArray(val, vueObj.DisallowedTypes);
                            if (arrindx == -1) {
                            }
                            else {
                                chkError = chkError + 1;
                            }
                            attchobj.push(
                                { "formBaseString": "" + vueObj.fileData[i].base64String + "", "attachementMime": "" + vueObj.fileData[i].attachement_mime + "", "containerName": "" + vueObj.fileData[i].containerName + "", "extensionName": "" + vueObj.fileData[i].extensionName + "", "fileSize": "" + vueObj.fileData[i].fileSize + "" }
                            );
                        }
                        if (chkError == 0) {
                            objectAttachment = JSON.stringify(attchobj);
                        }
                    }
                    else {
                        objectAttachment = null;
                    }
                }
                else {
                    objectAttachment = null;
                }

                if (chkError == 0) {
                    var objattachprev = "";
                    if (vueObj.AttachmentList.length > 0) {
                        attchobj = [];
                        for (var i = 0; i < vueObj.AttachmentList.length; i++) {
                            attchobj.push(
                                { "AttachmentId": "" + vueObj.AttachmentList[i].TICKET_ATTACHMENT_ID + "" }
                            );

                        }
                        objattachprev = JSON.stringify(attchobj);
                    }
                    else {
                        objattachprev = null;
                    }
                    var obj = {
                        IncidentId: vueObj.IncidentId,
                        TicketId: vueObj.ticketId,
                        WorkOrderId: vueObj.workOrderId,
                        ParentIncidentId: vueObj.ParentIncidentId,
                        ChildSubIncidentId: vueObj.ChildSubIncidentId,
                        IncidentAttachment: vueObj.FileSrc,
                        Description: vm.FormSchemaData[1].Data[0].value,
                        StatusId: 1001,
                        file: objectAttachment,
                        prevfiles: objattachprev,
                        ModuleName: 'Ticketing',
                        Submodulecode: 'Ticketing',
                    }
                    var objectPostString = JSON.stringify(obj);
                    DataServices.AddReportIncident(objectPostString).then(function (response) {
                            if (response.data != null && response.data != "") {
                                if (response.data.status == "Success") {
                                    if (response.data.status == "Success") {
                                        if (vueObj.IncidentId > 0) {
                                        alert('1');
                                            vueObj.$emit('Close');
                                        } else {
                                            vueObj.ShowAlert("Incident Succesfully Added")
                                            vueObj.$emit('Close');
                                        }
                                        // checkVue.noStatus=true;
                                    } else {
                                        ShowAlert(String.Format(Globalize.localize('ErrorWhileSavingRecords')), "failure", true, Globalize.localize('Alert'));
                                    }
                                } else {
                                    ShowAlert(String.Format(Globalize.localize('ErrorWhileSavingRecords')), "failure", true, Globalize.localize('Alert'));
                                }
                            } else {
                                ShowAlert(String.Format(Globalize.localize('ErrorWhileSavingRecords')), "failure", true, Globalize.localize('Alert'));
                            }
                        });
                }
                else {
                    var fileTypes = "";
                    for (var i = 0; i < vueObj.DisallowedTypes.length; i++) {
                        fileTypes = fileTypes + " " + vueObj.DisallowedTypes[i]
                    }
                    var errMsg = "";
                    errMsg = "File Types " + fileTypes + " are not allowed";

                    ShowAlert(errMsg, "failure", true, Globalize.localize('Alert'));
                }
            },     

        OnChange:function(e){
            debugger
            var vm=this;
            vm.ParentIncidentId=e.target.value;
            vm.SetParentIncident(vm.ParentIncidentId);
        },
        SetParentIncident:function(ParentIncidentId){
            debugger
            var vueObj = this;
            vueObj.FormSchemaData[0].Data[1].config.options=[];
            var IncidentSubTypeData = $.grep(vueObj.IncidentTypeList, function (element, index) {
                if(element.value == ParentIncidentId){
                    vueObj.FormSchemaData[0].Data[1].config.options.push({
                        name:element.name,
                        value:element.value
                    })
                }
            });
        }
    }
}
</script>