<template>
    <section>
        <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="widgets"
            IdentityColumn="SA_Id" :HeaderText="$t('ServiceAppointment')" :listType="['List']" :ListData="ReportIncidentListingData"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="totalRecord"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="fetchRecords" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
        <EditIncidentVue v-if="showIncident" @Close="Close" :EditIncidentData="EditIncidentData" :ticketId="ticketId"></EditIncidentVue>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import EditIncidentVue from './AddIncident.vue';
export default{
    components:{
        EditIncidentVue
    },
    data(){
        return{
            sortBy:'',
            sortExp:'',
            pageSize:10,
            pageNumber:1,
            showIncident:false,
            EditIncidentData:[],
            showEditIncident:true,
            ReportIncidentListingData:[],
            Headers: [
            {
                "COLUMN_NAME": "TYPE_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Incident Type",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "DESCRIPTION",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER": 2,
            }
        ],
        listActions:[  
        {
            title:'Edit',
            href: 'javascript:void(0)',
            callbackfunction: 'EditIncident',
            icon: 'fa action-icon fa fa-pencil',
            additionalSpan:''
        },         
        {
            title:'Delete',
            href: 'javascript:void(0)',
            icon: 'fa fa-trash',
            callbackfunction:'commonDeleteData',
            additionalSpan:''
        },          
    ]
        }
    },
    created:function(){
        var vm=this;
        vm.GetReportIncidentListing();
    },
    props:{
        workOrderId:{
            required:false
        },
        ticketId:{
            required:false
        }
    },
    methods:{
        openPopupForAddEditIncident:async function(id){
            var vm=this;
            var params=`Id=${id}`
            await DataService.IncidentTypeDetails(params).then(response => {
                vm.EditIncidentData=response.data.result.DATA;
                vm.showIncident=true;
            })
        },
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNumber = value.PageNumber;
            this.GetReportIncidentListing();
        },
        GetReportIncidentListing:function(){
            var vm=this;
            var params=`id=${vm.workOrderId}&ticketId=${vm.ticketId}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageSize=${vm.pageSize}&pageNumber=${vm.pageNumber}`
            DataService.FetchReportIncidentRecords(params).then(function (response) {
                vm.ReportIncidentListingData=response.data.result.data;
                vm.totalRecord=response.data.result.DATA[0].TOTALRECORDS;
            });           
            setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);

        },
        Close:function(){
            var vm=this;
            vm.showIncident=false;
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'EditIncident':
                    this.openPopupForAddEditIncident(item.ID)
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
    }
}
</script>
