<template>
    <section>
        <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
            RenderRowActionMethod="" v-on:ActionButtonClick="actionButtonClick" :widgets="widgets"
            IdentityColumn="SA_Id" :HeaderText="$t('ServiceAppointment')" :listType="['List']" :ListData="WorkOrderStatusHistory"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="totalRecord"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="fetchRecords" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
export default{
    props:{
        ticketId:{
            required:false
        },
        workOrderId:{
            required:false
        }
    },
    created:function(){
        var vm=this;
        vm.GetWorkOrderStatusHistory();
    },
    data(){
        return{
            Headers: [
            {
                "COLUMN_NAME": "NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "PARENT_SUB_STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "SUB_STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Sub Status",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "COMMENT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Comment",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "UPDATED_ON",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Updated At",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "SUPERVISOR_COMMENT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "SUPERVISOR COMMENT",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "FORMDATA",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Form",
                "DISPLAY_ORDER": 2,
            }
        ],
        pageNumber:1,
        pageSize:10,
        sortBy:'',
        sortExp:'',
        WorkOrderStatusHistory:[],
        totalRecord:0
        }
    },
    methods:{
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNumber = value.PageNumber;
            this.GetWorkOrderStatusHistory();
        },
        GetWorkOrderStatusHistory:function(){            
            var vm=this;
            var params=`workOrderId=${vm.workOrderId}&ticketingId=${vm.ticketId}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&pageSize=${vm.pageSize}&pageNumber=${vm.pageNumber}`
            DataService.FetchViewStatusHistoryRecords(params).then(function (response) {
                vm.WorkOrderStatusHistory=response.data.result.DATA;
                vm.totalRecord=response.data.result.DATA[0].TOTALRECORDS;
            })           
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
        },
    }
}
</script>
