<template>
    <!-- <h2>{{ TimeTrackHistoryData }}</h2> -->
    <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
                RenderRowActionMethod="" v-on:ActionButtonClick="actionButtonClick" :widgets="widgets"
                IdentityColumn="SA_Id" :HeaderText="$t('ServiceAppointment')" :listType="['List']" :ListData="TimeTrackHistoryData"
                :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="totalRecord"
                :SearchFields="leftSearchFields" :ListDataCallBackFunction="TimeTrackHistory" :showCheckBox="false"
                :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            </tg-list>
</template>
<script>
import DataService from '../../services/DataService';
export default{
   props:{
    ticketId:{
        required:false
    },
    workOrderId:{
        required:false
    }
   },
   data(){
    return{
        Headers: [
            {
                "COLUMN_NAME": "TRACK_DESCRIPTION",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "TRACK_START_TIME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Start Time",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "TRACK_END_TIME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "End Time",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "DURATION",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Duration (HH:MM:SS)",
                "DISPLAY_ORDER ": 3,
            }
        ],
        pageNumber:1,
        pageSize:10,
        sortBy:'',
        sortExp:'',
        TimeTrackHistoryData:[],
        totalRecord:0
    }
   },
   created:function(){
    debugger
        var vm=this;
        vm.TimeTrackHistory();
   },
   methods:{
        pagerMethod: function (value) {
            debugger
            var vm=this;
            vm.pageSize = value.PageSize;
            vm.pageNumber = value.PageNumber;
            vm.TimeTrackHistory();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.TimeTrackHistory();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        TimeTrackHistory:function(){          
            var vm=this;
            var url = `ticketingId=${vm.ticketId}&pageSize=${vm.pageSize}&pageNumber=${vm.pageNumber}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&workCodeId=${vm.workOrderId}`;           
            DataService.TrackHistoryList(url).then(function (response) {
                vm.TimeTrackHistoryData=response.data.result.DATA;
                vm.totalRecord=response.data.result.DATA[0].TOTALRECORDS;
            })           
            setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
        },
   },
  
}
</script>
