<template>
    <section>

        <div class="listing" v-if="!NoRecords">
            <div class="table-responsive table-fix-header">
                <table class="table table-bordered dt-responsive nowrap mt-0" id="tblProductCheckoutList">
                    <thead class="thead-bg">
                        <tr>
                            <th class="text-center">
                                <input class="chkItems chkAll" type="checkbox" id="chkAll" name="checkbox" />
                            </th>
                            <th><span> Product Name</span></th>
                            <th><span> Required Quantity</span></th>
                            <th><span> Available Quantity</span></th>
                            <th class="text-center"><span> Status</span></th>

                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item,index) in ProductCheckoutList" :key="index">
                            <td class="text-center" v-if="item.ASSIGNED_STATUS == 1">
                                <input class="chkItems" type="checkbox" v-bind:data-uniqueid="item.asset_catalogue_id"
                                v-on:click="showCheckoutButton($event)" v-bind:value="item.asset_catalogue_id"
                                    name="DeleteInputs" />
                            </td>
                            <td class="text-center" v-if="item.ASSIGNED_STATUS == 0">
                                <input class="chkItems" type="checkbox" v-bind:data-uniqueid="item.asset_catalogue_id"
                                v-on:click="showCheckoutButton($event)" v-bind:value="item.asset_catalogue_id"
                                    name="DeleteInputs" />
                            </td>
                            <td>{{ item.ASSET_TYPE }}</td>
                            <td>{{ item.REQUIRED_QUANTITY }}</td>
                            <td>{{ item.AVAILABLE_ITEMS }}</td>
                            <td class="text-center single-action">
                                <a v-if="item.ASSIGNED_STATUS == 1" data-toggle-tooltip="tooltip"
                                    data-original-title="Assigned" href="javascript:;">
                                    <i class="fa fa-check-circle text-success"></i>
                                </a>
                                <a v-if="item.ASSIGNED_STATUS == 0" data-toggle-tooltip="tooltip"
                                    data-original-title="Checkout" href="javascript:;">
                                    <i class="fa fa-share-square-o"></i>
                                </a>
                            </td>


                        </tr>

                        <tr v-if="NoRecords">
                            <td class="text-center text-danger no-record" colspan="7">
                                {{ $t('NoRecordfound') }}
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="row flex-row-reverse">
            <div class="col-lg-6 text-right mt-2">
                <a href="javascript:;" :title="$t('ProceedToCheckout')" v-show="isCheckoutButton" v-on:click="CheckoutProductRequest()"
                    class="btn btn-primary save-btn btnProductCheckout"><i class="fa fa-floppy-o pr-2"
                        aria-hidden="true"></i> {{ $t('ProceedToCheckout') }}</a>
            </div>
        </div>


    </section>
</template>

<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        workOrderId: {
            type: Number,
            required: false
        }
    },
    data() {
        return {
            ProductCheckoutList: [],
            NoRecords: false,
            isCheckoutButton:false,
            assetCatalogue: '',
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1"
        }
    },
    created() {
        var vm = this;
        vm.FetchRecords();
    },
    methods: {
        CheckoutProductRequest: function () {
            debugger;
            var vm = this;
            var assetCatalogueId = '';
            $('#tblProductCheckoutList tbody input:checkbox:checked').each(function () {
                if (assetCatalogueId.length > 0) {
                    assetCatalogueId += ",";
                }
                assetCatalogueId += $(this).val();
            });
            vm.assetCatalogue = btoa(assetCatalogueId);
            window.location.href = "/AssetAssignment/CheckOut?id=" + vm.assetCatalogue;
        },
        showCheckoutButton: function (event) {
            debugger;
            var vm=this;
            if (event.target.checked) {
                vm.isCheckoutButton = true;
            }
            else
            {
                vm.isCheckoutButton =false;
            }
        },
        FetchRecords: async function () {
            debugger;
            var vueObj = this;
            vueObj.ProductCheckoutList = [];

            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&workorderId=` + vueObj.workOrderId;
            await DataServices.GetProductcheckoutList(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.result.data.length > 0) {
                        vueObj.ProductCheckoutList = response.data.result.data;
                        vueObj.NoRecords = false;
                    } else {
                        vueObj.ProductCheckoutList = [];
                        vueObj.NoRecords = true;
                    }
                }
                else {
                    vueObj.ProductCheckoutList = [];
                    vueObj.NoRecords = true;
                }             
                setTimeout(function () { vueObj.ResponsiveDataTable('tblProductCheckoutList') }, 500);
            })
        }
    }
}
</script>