<template>
                <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
                :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="widgets"
                IdentityColumn="SA_Id" :HeaderText="$t('ServiceAppointment')" :listType="['List']" :ListData="SAData"
                :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                :SearchFields="leftSearchFields" :ListDataCallBackFunction="fetchRecords" :showCheckBox="false"
                :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'Status'">
                        <a class="active">
                            <span v-bind:class="getStatusClassList(data.row.Status, 'Status')">
                                {{ data.row[data.column.COLUMN_NAME] }}
                            </span>
                        </a>
                    </template>
                </template>
            </tg-list>
</template>
<script>
import DataService from '../../services/DataService';
export default{
    props:{
        workOrderId:{
            required:true
        }
    },
    data(){
        return{
            Headers: [
            {
                "COLUMN_NAME": "SA_NUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "SA Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "WORKORDER_NUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Order",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "TICKET_NUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Ticket Number",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "SA_SUBJECT",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Subject",
                "DISPLAY_ORDER ": 3,
            },
            {
                "COLUMN_NAME": "STATUS",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER ": 3,
            },
            {
                "COLUMN_NAME": "CLIENT_NAME",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Client Name",
                "DISPLAY_ORDER ": 3,
            },
            {
                "COLUMN_NAME": "SA_STREET",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Address",
                "DISPLAY_ORDER ": 3,
            },
            {
                "COLUMN_NAME": "ASSIGNED_NAME",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Assigned Name",
                "DISPLAY_ORDER ": 3,
            },
            {
                "COLUMN_NAME": "DATE",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Date",
                "DISPLAY_ORDER ": 3,
            },
            {
                "COLUMN_NAME": "TIME",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Time",
                "DISPLAY_ORDER ": 3,
            },
        ],
            TotalRecords: 0,
            SortBy: '',
            SortExp: '',
            SAData:[],
            searchCondition:null,
            PageSize: 10,
            PageNumber: 1
        }
    },
    created:function(){
        var vm=this;
        vm.fetchRecords();
    },
   methods:{
    async fetchRecords() { 
           
            var vueObj = this;        
            // vueObj.SAData = []; 
            var url = `id=${vueObj.workOrderId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&search=${vueObj.searchCondition}`;           
            await DataService.GetServiceAppointmentByWorkOrderId(url).then(response => {     
                        debugger
                        vueObj.SAData = response.data.result.DATA;
                        vueObj.TotalRecords = response.data.result.DATA[0].total_records;
            })            
             setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
        },
        renderActions: function (row) {
               
        },
        commonHeaderButtonClick:function(){

        },
        actionButtonClick(callbackfunction, item, index, event) {
                
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Completed': 
                case  'Dispatched':
                    returnClass = 'px-2 bg-success text-white text-center d-block';
                    break;
                case 'Cancelled':
                case 'Canceled':
                    returnClass = 'px-2 bg-danger text-white text-center d-block';
                    break;
                case 'Cannot Complete': 
                case 'Open':
                case 'Unscheduled':
                    returnClass = 'px-2 bg-secondary text-white text-center d-block';
                    break;
                case 'In Progress':
                    returnClass = 'px-2 bg-info text-white text-center d-block';
                    break;
                case 'Needs Reschedule':
                case 'Scheduled':
                    returnClass = 'px-2 bg-dark text-white text-center d-block';
                    break;
                case 'Failed':
                case 'On Hold' :
                case 'Need to Reschedule':             
                    returnClass = 'px-2 bg-warning text-white text-center d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.fetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.fetchRecords();
        },
   }
}
</script>
